<template>
    <div class='coachlist memberlist'>
        <!-- <div class='addMember' @click='addMember'>
            <img src='../../assets/images/btn_add.png'>
            <span></span>
        </div> -->
        <div class='top'>
          <div class='title'> <span>{{total}}</span></div>
          <div class='btn add' @click='addMember'>
              <i class='iconfont icon-btn_icon_add'></i>
              <span></span>
          </div>
          <div class='btn' @click='member'><i class='iconfont icon-btn_icon_input'></i></div>
        </div>
        <Table  :tableData="tableData" :tableQuery="tableQuery" :tabhide="tabhide" :tableKey="tableKey" ref="Table" :total="total" :isPage = "isPage" :pageSize = "pageSize" v-on:getQueryParam = "getQueryParam"
       v-on:changeVal="changeParam" v-on:getRowdata="getRowdata" :operWidth="operWidth"  :tabTxet='tabTxet'></Table>
       <card :dialogVisible="dialogVisible" :cardType="cardType" v-on:close ="close"></card>
       <memberCard :dialogFormVisible="CarddialogVisible" :text="cardText" :cardType="cardTypeMember" v-on:closeForm = "closeForm"></memberCard>
    </div>
</template>
<script>
import Table from '../../components/Table'
import Card from '../rechargeCard'
import memberCard from '@/components/memberCard'
import {member, queryCardList} from '../../services/service.js'
import {isApiResponseOk, setSessionStorage} from '../../utils/com.js'
export default {
  data () {
    return {
      tableQuery: [
        {
          name: 'physicalCardNo',
          title: '',
          type: 'input',
          model: '',
          text: ''
        },
        {
          name: 'personName',
          title: '',
          type: 'input',
          model: '',
          text: '',
          style: {float: 'inherit'}
        },
        {
          name: 'vipCardType',
          type: 'select',
          model: '',
          title: '',
          text: '',
          selectArr: [],
          style: {width: '67%'}
        },
        {
          name: 'status',
          type: 'select',
          model: '',
          title: '',
          text: '',
          selectArr: [],
          style: {width: '67%'}
        },
        {
          name: 'START_DATE',
          type: 'start_date',
          model: '',
          title: '',
          format: 'yyyy-MM-dd HH:mm:ss',
          text: ''
        },
        {
          name: 'END_DATE',
          type: 'end_date',
          model: '',
          title: '',
          format: 'yyyy-MM-dd HH:mm:ss',
          text: ''
        }
      ],
      tableKey: [
        {name: 'ID', value: 'code', nor: true},
        {name: '', value: 'cardName', nor: true},
        {name: '', value: 'vipCardType', nor: true},
        {name: '', value: 'personName', nor: true},
        {name: '', value: 'endTime', nor: true},
        {name: '', value: 'balance', balance: true},
        {name: '', value: 'status', nor: true},
        {name: '', value: 'createTime', nor: true},
        {name: '', value: 'btnGroup', btn: true}
      ],
      tableData: [
      ],
      isPage: true,
      total: 0,
      tabhide: false,
      pageSize: 10,
      para: {pageNo: 1, pageSize: 10},
      // operateBtn: [{text: ''},{text: ''}],
      operWidth: '180',
      dialogVisible: false, // 
      cardType: 'PREPAID',
      cardTypeMember: 'PREPAID',
      tabTxet: true,
      CarddialogVisible: false, // 
      cardText: ''
    }
  },
  components: {
    Table,
    Card,
    memberCard
  },
  created () {
    // 
    this.$nextTick(_ => {
      this.listSelect()
      this.initCardList(this.para)
    })
    // this.initStudent(this.para)
  },
  methods: {
    // 
    listSelect () {
      let option1 = [
        {
          value: '',
          label: ''
        },
        {
          value: 'PREPAID',
          label: ''
        },
        {
          value: 'TIMES',
          label: ''
        },
        {
          value: 'DATELIMIT',
          label: ''
        },
        {
          value: 'PACKAGE',
          label: ''
        }
      ]
      let option2 = [
        {
          value: '',
          label: ''
        },
        {
          value: 'NORMAL',
          label: ''
        },
        {
          value: 'INACTIVATION',
          label: ''
        },
        {
          value: 'STOP',
          label: ''
        },
        {
          value: 'CANCEL',
          label: ''
        }
      ]
      this.$refs.Table.reloadValue('vipCardType', option1)
      this.$refs.Table.reloadValue('status', option2)
    },
    getQueryParam (queryObj) {
      console.log('', queryObj)
      // this.currentList = []
      // this.IndexLsit = []
      this.para.pageNo = 1
      if (queryObj.physicalCardNo) {
        this.para.physicalCardNo = queryObj.physicalCardNo
      } else {
        delete this.para.physicalCardNo
      }
      if (queryObj.personName) {
        this.para.personName = queryObj.personName
      } else {
        delete this.para.personName
      }
      if (queryObj.vipCardType) {
        this.para.vipCardType = queryObj.vipCardType
      } else {
        delete this.para.vipCardType
      }
      if (queryObj.status) {
        this.para.status = queryObj.status
      } else {
        delete this.para.status
      }
      if (queryObj.START_DATE) {
        this.para.beginTime = queryObj.START_DATE
      } else {
        delete this.para.beginTime
      }
      if (queryObj.END_DATE) {
        this.para.endTime = queryObj.END_DATE
      } else {
        delete this.para.endTime
      }
      this.initCardList(this.para)
    },
    changeParam (data) {
      this.para.pageNo = data
      this.initCardList(this.para)
    },
    getRowdata (data) {
      if (data.index === '') {
        this.$router.push({name: 'memberDetail', query:{personId: data.row.personId, card: data.row.code, cardName: data.row.cardName}})
      } else if (data.index === '') {
        this.$store.commit('setPerson', data.row)
        this.cardType = data.row.vipCardType
        this.dialogVisible = true
      } else if (data.index === '') {
        this.$store.commit('setPerson', data.row)
        this.cardTypeMember = data.row.vipCardType
        this.CarddialogVisible = true
        this.cardText = ''
      } else if (data.index === '') {
        this.$store.commit('setPerson', data.row)
        this.cardTypeMember = data.row.vipCardType
        this.CarddialogVisible = true
        this.cardText = ''
      } else if (data.index === '') {
        this.$store.commit('setPerson', data.row)
        this.cardTypeMember = data.row.vipCardType
        this.CarddialogVisible = true
        this.cardText = ''
        console.log('', this.cardText)
      }
    },
    async initStudent (data) {
      const res = await member(data)
      if (isApiResponseOk(res)) {
        if (res.data) {
          this.tableData = res.data.studentSessionStubPOList
          this.total = res.data.count
          if (this.total === 0) {
            this.isPage = false
          } else {
            this.isPage = true
          }
        } else {
          this.isPage = false
        }
      } else {
        this.isPage = false
      }
    },
    // 
    async initCardList (data) {
      const res = await queryCardList(data)
      if (isApiResponseOk(res)) {
        this.tableData = res.data.record
        this.total = res.data.totalNum
        for (let val of res.data.record) {
          if (val.status === 'STOP') {
          val['btnGroup'] = [{text: ''}, {text: ''}, {text: ''}, {text: ''}]
          } else {
            val['btnGroup'] = [{text: ''}, {text: ''}, {text: ''}, {text: ''}]
          }
        }
        if (this.total === 0) {
          this.isPage = false
        } else {
            this.isPage = true
          }
        console.log('', this.tableData, this.total)
      } else {
        this.isPage = false
      }
    },
    // 
    addMember () {
      this.$router.push({name: 'addmembercard'})
    },
    // 
    close () {
      this.dialogVisible = false
      this.initCardList(this.para)
    },
    // 
    closeForm () {
      this.CarddialogVisible = false
      this.initCardList(this.para)
    },
    // 
    member () {
      this.$router.push({name: 'ImportCard'})
    }
  }
}
</script>
<style lang="sass" scoped>
 .top
    display: flex;
    height: .36rem;
    margin-bottom: .3rem;
    .btn
      font-family: PingFangSC-Medium;
      font-size: .15rem;
      color: #20C6BA;
      letter-spacing: 0;
      line-height: .15rem;
      height: .36rem;
      line-height: .36rem;
      border: 1px solid #20C6BA;
      border-radius: .02rem;
      width: 1.32rem;
      text-align: center;
      cursor: pointer;
    .add
      margin-right: .2rem;
    .title
      flex: 1;
      line-height: .36rem;
      span
        background: #E9EDEF;
        border-radius: 100px;
        font-size: .2rem;
        color: #8597A1;
        padding: .03rem .14rem;
        margin-left: .1rem;
    .icon-btn_icon_input:before
      margin-right: .12rem;
</style>




